import { useState, useEffect } from 'react';
import { Box, Typography } from '@material-ui/core';
import Hakukentta from './components/Hakukentta';
import ListaaHakutulos from './components/ListaaHakutulos';
import SuosikitOtsikko from './components/SuosikitOtsikko';
import ListaaSuosikit from './components/ListaaSuosikit';
import Virhe from './Error.png';

function App() {

	const [elokuvat, setElokuvat] = useState([]);
	const [suosikit, setSuosikit] = useState([]);
	const [haettava, setHaettava] = useState('');
	const [kuva, setKuva] = useState('');
	const [yhteysVirhe, setYhteysVirhe] = useState(false);
	const [kuvaVirhe, setKuvaVirhe] = useState(false);


	const lataaSuosikit = () => {

		if (localStorage.getItem("suosikit")) {
	
		  setSuosikit(JSON.parse(localStorage.getItem("suosikit")));
	
		} else {
	
		  setSuosikit([]);
		}
	
	}

	const tallennaSuosikit = () => {

		localStorage.setItem("suosikit", JSON.stringify(suosikit));
	
	}

	useEffect(() => {

		lataaSuosikit();
	
	}, []);
	
	useEffect(() => {
	
		tallennaSuosikit();
	
	}, [suosikit]); 

	const haeElokuvat = async () => {

		try {
		
		const url = `https://www.omdbapi.com/?s=${haettava}&apikey=a943e9ee`;

		const response = await fetch(url);
		const responseJson = await response.json();

		if (responseJson.Search) {
			setElokuvat(responseJson.Search.filter(tulos => tulos.Type === "movie"));
		}

		} catch (error) {
			setYhteysVirhe(true);
		}

	}

	useEffect(() => {
		haeElokuvat();

	}, [haettava]);

	useEffect(() => {
		setKuvaVirhe(false);

	}, [kuva]);

	const eiJulistetta = () => {
		setKuvaVirhe(true);
	}

	return (

		<Box >
			<Box 
				display="flex"
				alignItems="flex-start"
				flexDirection="row"
				p={0}
				m={0}
				sx={{ width: 1280 }}
			>
			<Box style={{maxHeight: '20vh', overflow: 'auto'}} sx={{ width: 490 }} p={1} flexGrow={0} >
				<SuosikitOtsikko suosikit={suosikit} setSuosikit={setSuosikit}/>
			</Box>
			<Box p={1} sx={{ width: 300}} justify="center" flexGrow={0}>
				<Typography align="center"><h2>Elokuvasovellus</h2></Typography>
			</Box>
			<Box style={{maxHeight: '20vh', overflow: 'auto'}} sx={{ width: 490 }} flexGrow={0} p={1}  >
				<Hakukentta haettava={haettava} setHaettava={setHaettava} />
			</Box>
		</Box>		

		<Box 
			display="flex"
			alignItems="flex-start"
			flexDirection="row"
			p={0}
			m={0}
			sx={{ width: 1280 }}
	  	>
  
			<Box style={{maxHeight: '80vh', overflow: 'auto'}} sx={{ width: 490 }} p={1} flexGrow={0} >
				<ListaaSuosikit suosikit={suosikit} setSuosikit={setSuosikit} kuva={kuva} setKuva={setKuva}/>
			</Box>
			<Box p={1} sx={{ width: 300, height: "30%" }} justify="center" flexGrow={0}>
				{(kuva !== '' )			  			  
				? <img
				style={{ maxWidth: "100%" }}
				src={  kuvaVirhe 
					? Virhe
					: kuva}
				onError={() => eiJulistetta()}
				alt="Juliste"
				/>
				: ""
				}
			</Box>
			<Box style={{maxHeight: '80vh', overflow: 'auto'}} sx={{ width: 490 }} flexGrow={0} p={1}  >
				<ListaaHakutulos yhteysVirhe={yhteysVirhe} elokuvat={elokuvat} suosikit={suosikit} setSuosikit={setSuosikit} kuva={kuva} setKuva={setKuva}/>
			</Box>
	 	 </Box>
	  </Box>
	)
}

export default App;
