import { List, ListItem, ListItemText, IconButton, ListItemIcon, Tooltip, Typography } from "@material-ui/core";
import SortByAlphaIcon from '@material-ui/icons/SortByAlpha';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import SwapVertIcon from '@material-ui/icons/SwapVert';

function SuosikitOtsikko(props) {

    const aakkosiin = () => {

        let aakkosApu = props.suosikit.sort((nyt,seuraava) => {

            return nyt.Title.localeCompare(seuraava.Title) || Number(nyt.Year - seuraava.Year)
        
        });

            props.setSuosikit([...aakkosApu]);
    
    }

    const kaanna = () => {

        let kaantoApu = props.suosikit.reverse();

            props.setSuosikit([...kaantoApu]);
        
    }
        

    const aikajarjestykseen = () => {
    
        let vuosiApu = props.suosikit.sort((nyt,seuraava) => {

            return Number(nyt.Year - seuraava.Year) || nyt.Title.localeCompare(seuraava.Title)
        
        });

        props.setSuosikit([...vuosiApu]);

    }

    return (

        <>

            <List>
                <ListItem>
                    <ListItemText primary={<Typography variant="h6">Suosikkielokuvia</Typography>} />
                    <ListItemIcon>
                        <Tooltip title="Aakkosjärjestykseen" placement="bottom" enterDelay={1000} enterNextDelay={1000}>
                            <IconButton 
                                edge="end"
                                    onClick={() => {
                                        aakkosiin();
                                    }}
                                >
                                    <SortByAlphaIcon />
                            </IconButton>
                        </Tooltip>
                    </ListItemIcon>
                    <ListItemIcon>
                        <Tooltip title="Käänteiseen järjestykseen" placement="bottom" enterDelay={1000} enterNextDelay={1000}>
                            <IconButton 
                                edge="end"
                                    onClick={() => {
                                        kaanna();
                                    }}
                                >
                                    <SwapVertIcon />
                            </IconButton>
                        </Tooltip>
                    </ListItemIcon>      
                    <ListItemIcon>
                        <Tooltip title="Aikajärjestykseen" placement="right" enterDelay={1000} enterNextDelay={1000}>
                            <IconButton 
                                edge="end"
                                    onClick={() => {
                                        aikajarjestykseen();
                                    }}
                                >
                                    <CalendarTodayIcon />
                            </IconButton>
                        </Tooltip>
                    </ListItemIcon>   
                </ListItem>

            </List>

        </>
    )
}


export default SuosikitOtsikko;
