import { List, ListItem, ListItemText, IconButton, ListItemIcon, Tooltip } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';

function ListaaSuosikit(props) {

    const poistaSuosikki = (poistettava) => {

        let suosikitApu = props.suosikit.filter((nyt) => {
            return (nyt.imdbID !== poistettava.imdbID);
        });

        props.setSuosikit([...suosikitApu]);

    }

    return (

        <>

            {(props.suosikit.length === 0)
                ? <List><ListItem><ListItemText primary={"Suosikkilista on tyhjä, hae ja lisää elokuvia."}/></ListItem></List>
                : <List>

                    { props.suosikit.map( (suosikki, idx) => {
                
                        return (
                            
                                <ListItem onMouseOver={() => {props.setKuva(suosikki.Poster)}} variant="contained" key={idx}>
                                    <ListItemText primary={suosikki.Title + " (" + suosikki.Year + ")"} />
                                    <ListItemIcon>
                                        <Tooltip title="Poista suosikeista" placement="right" enterDelay={1000} enterNextDelay={1000}>
                                        <IconButton 
                                            edge="end"
                                            onClick={() => {
                                                poistaSuosikki(suosikki);
                                            }}
                                        >
                                            <DeleteIcon />
                                        </IconButton>
                                        </Tooltip>
                                    </ListItemIcon>                 
                                </ListItem>                    
                            );
                
                        } ) }
            
                </List>
                
            }

        </>
    )
}


export default ListaaSuosikit;
