import { List, ListItem, ListItemText, IconButton, ListItemIcon, Tooltip } from "@material-ui/core";
import FavoriteIcon from '@material-ui/icons/Favorite';
import BeenhereIcon from '@material-ui/icons/Beenhere';

function ListaaHakutulos(props) {

    const lisaaSuosikki = (uusiSuosikki) => {

        let uusiLista = [...props.suosikit, uusiSuosikki];

        let tuplaApu = props.suosikit.filter((nyt) => {
            return (uusiSuosikki.imdbID === nyt.imdbID);
        });
  
        if (tuplaApu.length === 0) {
            props.setSuosikit(uusiLista);
        }
 
    }

    return (

        <>
      
            {(props.yhteysVirhe)
                
                ? <List><ListItem><ListItemText primary={"VIRHE: yhteyttä palvelimelle ei voitu muodostaa tai muu häiriö."}/></ListItem></List>
                : (props.elokuvat.length === 0)
                    ? <List><ListItem><ListItemText primary={"Ei vielä hakutuloksia."}/></ListItem></List>
                    : <List>

                        { props.elokuvat.map( (elokuva, idx) => {
            
                        return (
                            
                                <ListItem onMouseOver={() => {props.setKuva(elokuva.Poster)}} variant="contained" key={idx}>
                                    <ListItemText primary={elokuva.Title + " (" + elokuva.Year + ")"} />
                                    {((props.suosikit.filter((nyt) => {return (elokuva.imdbID === nyt.imdbID)})).length === 0)
                                            ? <ListItemIcon>
                                                <Tooltip title="Lisää suosikkeihin" placement="right" enterDelay={1000} enterNextDelay={1000}>
                                                <IconButton 
                                                    onClick={() => {
                                                        lisaaSuosikki(elokuva);
                                                    }}
                                                >
                                                    <FavoriteIcon />
                                                </IconButton>
                                                </Tooltip>
                                                </ListItemIcon>     
                                            : <ListItemIcon>             
                                            <IconButton disabled={true}>
                                                <BeenhereIcon />
                                            </IconButton>
                                            </ListItemIcon>  
                                        }
                        
                                </ListItem>                    
                            );
                
                        } ) }
        
                    </List>
            }

        </>
    )
}

export default ListaaHakutulos;
