import { TextField } from '@material-ui/core';

function Hakukentta(props) {
	return (
		<div>
            <TextField 
                name="haku"
                label="Hae elokuvia Open Movie Databasesta"
                variant="outlined"
                fullWidth={true}
                onChange={(e) => {
                    props.setHaettava(e.target.value)}}
                style={{marginBottom : 10}}
            />
		</div>
	);
};

export default Hakukentta;